<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            clearable
            @clear="getEquipmentList"
          >
            <el-button slot="append" icon="el-icon-search" @click="getEquipmentList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="equipmentlist" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="equipmentName" label=""></el-table-column>
        <el-table-column prop="equipmentPicture" label="">
          <template slot-scope="scope">
            <img :src="'http://101.132.135.179:8080'+scope.row.equipmentPicture" width="150px" height="100px"/>
          </template>
        </el-table-column>
        <el-table-column prop="equipmentIntro" label="" width="150px"></el-table-column>
        <el-table-column prop="equipmentUsage" label="" sortable></el-table-column>
        <el-table-column prop="equipmentNum" label="" sortable></el-table-column>
        <el-table-column label="" width="180px">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="showEquipmentDialog(scope.row.id)"
            ></el-button>
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="removeEquipmentById(scope.row.id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.page"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.limit"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>

      <el-row :gutter="20">
        <el-col :span="4">
          <el-button type="danger" style="margin:10px 0 0 0"></el-button>
        </el-col>
      </el-row>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <!--  -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
        <el-form-item label="" prop="equipmentName">
          <el-input v-model="addForm.equipmentName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="equipmentIntro">
          <el-input v-model="addForm.equipmentIntro"></el-input>
        </el-form-item>
        <el-form-item label="" prop="equipmentNum">
          <el-input v-model="addForm.equipmentNum"></el-input>
        </el-form-item>
        <el-form-item label="" prop="equipmentUsage">
          <el-input v-model="addForm.equipmentUsage"></el-input>
        </el-form-item>
        <el-form-item label="" prop="file">
          <el-upload
            ref="upload"
            action="http://101.132.135.179:8088/hotel/fitness/insertFitness"
            :limit="limitNum"
            :auto-upload="false"
            accept=".jpg"
            :before-upload="beforeUploadFile"
            :on-change="fileChange"
            :on-exceed="exceedFile"
            :file-list="fileList"
          >
            <el-button size="small" plain></el-button>
            <div slot="tip" class="el-upload__tip" style="color:grey;">jpg10M</div>
          </el-upload>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addEquipment"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="90px">
        <el-form-item label="" prop="equipmentName">
          <el-input v-model="editForm.equipmentName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="equipmentIntro">
          <el-input v-model="editForm.equipmentIntro"></el-input>
        </el-form-item>
        <el-form-item label="" prop="equipmentNum">
          <el-input v-model="editForm.equipmentNum"></el-input>
        </el-form-item>
        <el-form-item label="" prop="equipmentUsage">
          <el-input v-model="editForm.equipmentUsage"></el-input>
        </el-form-item>
        <el-form-item label="" prop="file">
          <el-upload
            ref="upload"
            action="http://101.132.135.179:8088/hotel/fitness/updateFitness"
            :limit="limitNum"
            :auto-upload="false"
            accept=".jpg"
            :before-upload="beforeUploadFile"
            :on-change="fileChange"
            :on-exceed="exceedFile"
            :file-list="fileList"
          >
            <el-button size="small" plain></el-button>
            <div slot="tip" class="el-upload__tip" style="color:grey;">jpg10M</div>
          </el-upload>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editEquipmentInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryInfo: {
        // query: "",
        // 
        page: 1,
        // 
        limit: 5
      },
      total: 0,
      equipmentlist: [],
      addDialogVisible: false,
      // 
      limitNum: 1,
      // 
      addForm: {
        file: "",
        equipmentName: "",
        equipmentIntro: "",
        equipmentNum: "",
        equipmentUsage: ""
      },
      fileList: [],
      // 
      addFormRules: {
        equipmentName: [
          { required: true, message: "", trigger: "blur" }
        ],
        equipmentIntro: [
          { required: true, message: "", trigger: "blur" }
        ],
        equipmentNum: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      // 
      editDialogVisible: false,
      // 
      editForm: {},
      // 
      editFormRules: {
        equipmentName: [
          { required: true, message: "", trigger: "blur" }
        ],
        equipmentIntro: [
          { required: true, message: "", trigger: "blur" }
        ],
        equipmentNum: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  created() {
    this.getEquipmentList();
  },
  methods: {
    async getEquipmentList() {
      const { data: res } = await this.$http.get(
        "http://101.132.135.179:8088/hotel/select/getFitnessList",
        {
          params: this.queryInfo
        }
      );

      if (res.code !== 0) {
        return this.$message.error("");
      }
      this.$message.success("");

      console.log(res);
      this.total = res.count;
      this.equipmentlist = res.data;
    },
    handleSizeChange(newSize) {
      this.queryInfo.limit = newSize;
      this.getEquipmentList();
    },
    handleCurrentChange(newPage) {
      this.queryInfo.page = newPage;
      this.getEquipmentList();
    },
    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields();
      this.$refs.upload.clearFiles();
    },
    // 
    addEquipment() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return;
        let formData = new FormData();
        formData.append("file", this.addForm.file);
        // 
        const {
          data: res
        } = await this.$http.post(
          "http://101.132.135.179:8088/hotel/fitness/insertFitness?" +
            "equipmentName=" +
            this.addForm.equipmentName +
            "&equipmentIntro=" +
            this.addForm.equipmentIntro +
            "&equipmentNum=" +
            this.addForm.equipmentNum +
            "&equipmentUsage=" +
            this.addForm.equipmentUsage,
          formData,
          { "Content-Type": "multipart/form-data" }
        );

        if (res.code == 0) {
          this.$message.success("");
        } else if (res.code == 1000) {
          this.$message.warning("");
        } else {
          this.$message.error("");
        }

        // 
        this.addDialogVisible = false;
        // 
        this.getEquipmentList();
      });
    },
    // 
    async showEquipmentDialog(id) {
      console.log(id);
      const { data: res } = await this.axios.post(
        "http://101.132.135.179:8088/hotel/select/getFitnessById?id=" + id
      );

      console.log("id", res);
      if (res.code != 0) {
        return this.$message.success("");
      }
      this.$message.success("");
      this.editForm = res.data;
      this.editForm.password = "";
      console.log("", res.data);
      this.editDialogVisible = true;
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields();
      this.$refs.upload.clearFiles();
    },
    // 
    editEquipmentInfo() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return;
        // 
        let formData1 = new FormData();
        formData1.append("file", this.editForm.file);
        const { data: res } = this.$http
          .post(
            "http://101.132.135.179:8088/hotel/fitness/updateFitness?id=" +
              this.editForm.id +
              "&equipmentName=" +
              this.editForm.equipmentName +
              "&equipmentIntro=" +
              this.editForm.equipmentIntro +
              "&equipmentNum=" +
              this.editForm.equipmentNum +
              "&equipmentUsage=" +
              this.editForm.equipmentUsage +
              "&equipmentStates=" +
              this.editForm.equipmentStates,
            formData1,
            { "Content-Type": "multipart/form-data" }
          )
          .then(res => {
            if (res.data.code !== 0) {
              return this.$message.error("");
            }
            // 
            this.$message.success("");
            // 
            this.editDialogVisible = false;
            // 
            this.getEquipmentList();
          })
          .catch(err => {});
      });
    },
    // Id
    async removeEquipmentById(id) {
      console.log(id);
      // 
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => err);

      //  confirm
      //  cancel
      // console.log(confirmResult)
      if (confirmResult !== "confirm") {
        return this.$message.info("");
      }

      const { data: res } = await this.axios
        .post(
          "http://101.132.135.179:8088/hotel/fitness/recycleFitness?id=" + id
        )
        .then(res => {
          console.log("res", res);
          if (res.code != 0) {
            return this.$message.success("");
          }
          this.$message.error("");
        });
      this.getEquipmentList();
    },
    // 
    exceedFile(files, fileList) {
      this.$notify.warning({
        title: "",
        message: ` ${
          this.limitNum
        }  ${files.length + fileList.length} `
      });
    },
    // 
    fileChange(file, fileList) {
      console.log("change");
      console.log(file);
      this.addForm.file = file.raw;
      console.log(this.addForm.file);
      console.log(fileList);
    },
    // , , false  Promise  reject
    beforeUploadFile(file) {
      console.log("before upload");
      console.log(file);
      let extension = file.name.substring(file.name.lastIndexOf(".") + 1);
      let size = file.size / 1024 / 1024;
      if (extension !== "jpg") {
        this.$notify.warning({
          title: "",
          message: `jpg.jpg`
        });
      }
      if (size > 10) {
        this.$notify.warning({
          title: "",
          message: `10M`
        });
      }
    }
    // // 
    // handleSuccess(res, file, fileList) {
    //   this.$notify.success({
    //     title: "",
    //     message: ``
    //   });
    // },
    // // 
    // handleError(err, file, fileList) {
    //   this.$notify.error({
    //     title: "",
    //     message: ``
    //   });
    // }
  }
};
</script>

<style lang="stylus">
.el-cascader {
  width: 100%;
}
</style>
