<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getGoodList">
            <el-button slot="append" icon="el-icon-search" @click="getGoodList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="goodlist" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="productName" label=""></el-table-column>
        <el-table-column prop="productPicture" label="">
          <template slot-scope="scope">
            <img :src="'http://101.132.135.179:8080'+scope.row.productPicture" width="150px" height="100px" />
          </template>
        </el-table-column>
        <el-table-column prop="productIntro" label="" width="150px"></el-table-column>
        <el-table-column
          prop="productType"
          label=""
          width="150"
          column-key="productType"
          :filters="[{text: '', value: ''}, {text: '', value: ''}, {text: '', value: ''}, {text: '', value: ''}]"
        ></el-table-column>
        <el-table-column prop="productUnitPrice" label="" sortable></el-table-column>
        <el-table-column prop="productNum" label="" sortable></el-table-column>
        <el-table-column label="" width="180px">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="showGoodDialog(scope.row.id)"
            ></el-button>
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="removeGoodById(scope.row.id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.page"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.limit"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>

      <el-row :gutter="20">
        <el-col :span="4">
          <el-button type="danger" style="margin:10px 0 0 0"></el-button>
        </el-col>
      </el-row>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
      <!--  -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="80px">
        <el-form-item label="" prop="productName">
          <el-input v-model="addForm.productName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="productIntro">
          <el-input v-model="addForm.productIntro"></el-input>
        </el-form-item>
        <el-form-item label="" prop="productType">
          <el-input v-model="addForm.productType"></el-input>
        </el-form-item>
        <el-form-item label="" prop="productUnitPrice">
          <el-input v-model="addForm.productUnitPrice"></el-input>
        </el-form-item>
        <el-form-item label="" prop="productNum">
          <el-input v-model="addForm.productNum"></el-input>
        </el-form-item>
        <el-form-item label="" prop="file">
          <el-upload
            ref="upload"
            action="http://101.132.135.179:8088/hotel/product/insertProduct"
            :limit="limitNum"
            :auto-upload="false"
            accept=".jpg"
            :before-upload="beforeUploadFile"
            :on-change="fileChange"
            :on-exceed="exceedFile"
            :file-list="fileList"
          >
            <el-button size="small" plain></el-button>
            <div slot="tip" class="el-upload__tip" style="color:grey;">jpg10M</div>
          </el-upload>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addGood"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
        <el-form-item label="" prop="productName">
          <el-input v-model="editForm.productName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="productIntro">
          <el-input v-model="editForm.productIntro"></el-input>
        </el-form-item>
        <el-form-item label="" prop="productType">
          <el-input v-model="editForm.productType"></el-input>
        </el-form-item>
        <el-form-item label="" prop="productUnitPrice">
          <el-input v-model="editForm.productUnitPrice"></el-input>
        </el-form-item>
         <el-form-item label="" prop="productNum">
          <el-input v-model="editForm.productNum"></el-input>
        </el-form-item>
        <el-form-item label="" prop="file">
          <el-upload
            ref="upload"
            action="http://101.132.135.179:8088/hotel/product/updateProduct"
            :limit="limitNum"
            :auto-upload="false"
            accept=".jpg"
            :before-upload="beforeUploadFile"
            :on-change="fileChange"
            :on-exceed="exceedFile"
            :file-list="fileList"
          >
            <el-button size="small" plain></el-button>
            <div slot="tip" class="el-upload__tip" style="color:grey;">jpg10M</div>
          </el-upload>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editGoodInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryInfo: {
        // query: "",
        // 
        page: 1,
        // 
        limit: 5
      },
      total: 0,
      goodlist: [],
      addDialogVisible: false,
      // 
      limitNum: 1,
      // 
      addForm: {
        file: "",
        productName: "",
        productPicture: "",
        productIntro: "",
        productType: "",
        productUnitPrice: "",
        productNum: ""
      },
      fileList: [],
      // 
      addFormRules: {
        productName: [{ required: true, message: "", trigger: "blur" }],
        productIntro: [
          { required: true, message: "", trigger: "blur" }
        ],
        productType: [
          { required: true, message: "", trigger: "blur" }
        ],
        productUnitPrice: [
          { required: true, message: "", trigger: "blur" }
        ],
        productNum: [
          { required: true, message: "", trigger: "blur" }
        ],
      },
      // 
      editDialogVisible: false,
      // 
      editForm: {},
      // 
      editFormRules: {}
    };
  },
  created() {
    this.getGoodList();
  },
  methods: {
    async getGoodList() {
      const { data: res } = await this.$http.get(
        "http://101.132.135.179:8088/hotel/select/getProductList",
        {
          params: this.queryInfo
        }
      );

      if (res.code !== 0) {
        return this.$message.error("");
      }
      this.$message.success("");

      console.log(res);
      this.total = res.count;
      this.goodlist = res.data;
    },
    handleSizeChange(newSize) {
      this.queryInfo.limit = newSize;
      this.getGoodList();
    },
    handleCurrentChange(newPage) {
      this.queryInfo.page = newPage;
      this.getGoodList();
    },
    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields();
      this.$refs.upload.clearFiles();
    },
    // 
    addGood() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return;
        let formData = new FormData();
        formData.append("file", this.addForm.file);
        // 
        const {
          data: res
        } = await this.$http.post(
          "http://101.132.135.179:8088/hotel/product/insertProduct?" +
            "productName=" +
            this.addForm.productName +
            "&productIntro=" +
            this.addForm.productIntro +
            "&productType=" +
            this.addForm.productType +
            "&productUnitPrice=" +
            this.addForm.productUnitPrice +
            "&productNum=" +
            this.addForm.productNum,
          formData,
          { "Content-Type": "multipart/form-data" }
        );

        if (res.code == 0) {
          this.$message.success("");
        } else if (res.code == 1000) {
          this.$message.warning("");
        } else {
          this.$message.error("");
        }

        // 
        this.addDialogVisible = false;
        // 
        this.getGoodList();
      });
    },
    // 
    async showGoodDialog(id) {
      console.log(id)
     const { data: res } = await this.axios.post(
        "http://101.132.135.179:8088/hotel/select/getProductById?id=" + id
      );

      console.log("id", res);
      if (res.code != 0) {
        return this.$message.success("");
      }
      this.$message.success("");
      this.editForm = res.data;
      this.editForm.password = "";
      console.log("", res.data);
      this.editDialogVisible = true;
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields();
      this.$refs.upload.clearFiles();
    },
    // 
    editGoodInfo() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return;
        // 
        let formData1 = new FormData();
        formData1.append("file", this.editForm.file);
        const { data: res } = this.$http
          .post(
            "http://101.132.135.179:8088/hotel/product/updateProduct?id=" +
              this.editForm.id +
              "&productName=" +
              this.editForm.productName +
              "&productIntro=" +
              this.editForm.productIntro +
              "&productType=" +
              this.editForm.productType +
              "&productUnitPrice=" +
              this.editForm.productUnitPrice+
               "&productNum=" +
              this.editForm.productNum,
            formData1,
            { "Content-Type": "multipart/form-data" }
          )
          .then(res => {
            if (res.data.code !== 0) {
              return this.$message.error("");
            }
            // 
            this.$message.success("");
            // 
            this.editDialogVisible = false;
            // 
            this.getGoodList();
          })
          .catch(err => {});
      });
    },
    // Id
    async removeGoodById(id) {
      console.log(id)
      // 
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => err);

      //  confirm
      //  cancel
      // console.log(confirmResult)
      if (confirmResult !== "confirm") {
        return this.$message.info("");
      }

       const { data: res } = await this.axios
        .post("http://101.132.135.179:8088/hotel/product/recycleProduct?id=" + id)
        .then(res => {
          console.log("res", res);
          if (res.code != 0) {
            return this.$message.success("");
          }
          this.$message.error("");
        });
      this.getGoodList();
    },
    // 
    exceedFile(files, fileList) {
      this.$notify.warning({
        title: "",
        message: ` ${
          this.limitNum
        }  ${files.length + fileList.length} `
      });
    },
    // 
    fileChange(file, fileList) {
      console.log("change");
      console.log(file);
      this.addForm.file = file.raw;
      console.log(this.addForm.file);
      console.log(fileList);
    },
    // , , false  Promise  reject
    beforeUploadFile(file) {
      console.log("before upload");
      console.log(file);
      let extension = file.name.substring(file.name.lastIndexOf(".") + 1);
      let size = file.size / 1024 / 1024;
      if (extension !== "jpg") {
        this.$notify.warning({
          title: "",
          message: `jpg.jpg`
        });
      }
      if (size > 10) {
        this.$notify.warning({
          title: "",
          message: `10M`
        });
      }
    }
    // // 
    // handleSuccess(res, file, fileList) {
    //   this.$notify.success({
    //     title: "",
    //     message: ``
    //   });
    // },
    // // 
    // handleError(err, file, fileList) {
    //   this.$notify.error({
    //     title: "",
    //     message: ``
    //   });
    // }
  }
};
</script>

<style lang="stylus">
.el-cascader {
  width: 100%;
}
</style>
