<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            clearable
            @clear="getBackgroundList"
          >
            <el-button slot="append" icon="el-icon-search" @click="getBackgroundList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="backgroundlist" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="username" label=""></el-table-column>
        <el-table-column prop="facePath" label="">
          <template slot-scope="scope">
            <img :src="'http://101.132.135.179:8080'+scope.row.facePath" width="100px" height="100px" />
          </template>
        </el-table-column>
        <el-table-column label="" width="100px">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="removeBackgroundById(scope.row.id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.page"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.limit"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>

      <el-row :gutter="20">
        <el-col :span="4">
          <el-button type="danger" style="margin:10px 0 0 0"></el-button>
        </el-col>
      </el-row>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
      <!--  -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="110px">
        <el-form-item label="" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="file">
          <el-upload
            ref="upload"
            action="http://101.132.135.179:8088/hotel/face/faceAdd"
            :limit="limitNum"
            :auto-upload="false"
            accept=".jpg"
            :before-upload="beforeUploadFile"
            :on-change="fileChange"
            :on-exceed="exceedFile"
            :file-list="fileList"
          >
            <el-button size="small" plain></el-button>
            <div slot="tip" class="el-upload__tip" style="color:grey;">png10M</div>
          </el-upload>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addbackground"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryInfo: {
        // query: "",
        // 
        page: 1,
        // 
        limit: 5
      },
      total: 0,
      backgroundlist: [],
      addDialogVisible: false,
      // 
      limitNum: 1,
      // 
      addForm: {
        userId: "",
        file: "",
        username: ""
      },
      fileList: [],
      // 
      addFormRules: {
        file: [{ required: true, message: "", trigger: "blur" }]
      }
    };
  },
  created() {
    this.getBackgroundList();
  },
  methods: {
    async getBackgroundList() {
      const { data: res } = await this.$http.get(
        "http://101.132.135.179:8088/hotel/select/getUserFaceInfoList",
        {
          params: this.queryInfo
        }
      );

      if (res.code !== 0) {
        return this.$message.error("");
      }
      this.$message.success("");

      console.log(res);
      this.total = res.count;
      this.backgroundlist = res.data;
    },
    handleSizeChange(newSize) {
      this.queryInfo.limit = newSize;
      this.getBackgroundList();
    },
    handleCurrentChange(newPage) {
      this.queryInfo.page = newPage;
      this.getBackgroundList();
    },
    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields();
      this.$refs.upload.clearFiles();
    },
    // 
    addbackground() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return;
        let formData = new FormData();
        formData.append("file", this.addForm.file);
        // 
        const {
          data: res
        } = await this.$http.post(
          "http://101.132.135.179:8088/hotel/face/faceAdd?username=" +
            this.addForm.username,
          formData,
          { "Content-Type": "multipart/form-data" }
        );

        if (res.code == 0) {
          this.$message.success("");
        } else if (res.code == 1000) {
          this.$message.warning("");
        } else {
          this.$message.error("");
        }

        // 
        this.addDialogVisible = false;
        // 
        this.getBackgroundList();
      });
    },
    // Id
    async removeBackgroundById(id) {
      console.log(id);
      // 
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => err);

      //  confirm
      //  cancel
      // console.log(confirmResult)
      if (confirmResult !== "confirm") {
        return this.$message.info("");
      }

      const { data: res } = await this.axios
        .post("http://101.132.135.179:8088/hotel/face/deleteFace?id=" + id)
        .then(res => {
          console.log("res", res);
          if (res.code != 0) {
            return this.$message.success("");
          }
          this.$message.error("");
        });
      this.getBackgroundList();
    },
    // 
    exceedFile(files, fileList) {
      this.$notify.warning({
        title: "",
        message: ` ${
          this.limitNum
        }  ${files.length + fileList.length} `
      });
    },
    // 
    fileChange(file, fileList) {
      console.log("change");
      console.log(file);
      this.addForm.file = file.raw;
      console.log(this.addForm.file);
      console.log(fileList);
    },
    // , , false  Promise  reject
    beforeUploadFile(file) {
      console.log("before upload");
      console.log(file);
      let extension = file.name.substring(file.name.lastIndexOf(".") + 1);
      let size = file.size / 1024 / 1024;
      if (extension !== "jpg") {
        this.$notify.warning({
          title: "",
          message: `jpg.jpg`
        });
      }
      if (size > 10) {
        this.$notify.warning({
          title: "",
          message: `10M`
        });
      }
    }
  }
};
</script>

<style lang="stylus">
.el-cascader {
  width: 100%;
}
</style>
