<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <p style="margin:0 0 10px 0;font-size:18px;"></p>
      <el-table :data="userlist" border stripe style="margin:15px 0 15px 0">
        <el-table-column label="" prop="username"></el-table-column>
        <el-table-column label="" prop="roomNumber"></el-table-column>
        <el-table-column label="" prop="roomPrice"></el-table-column>
        <el-table-column label="" prop="roomType"></el-table-column>
        <el-table-column  label="" prop="orderStates">
           <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.orderStates === 1"></el-tag>
            <el-tag type="warning" v-else-if="scope.row.orderStates === 2"></el-tag>
          </template>
        </el-table-column>
      </el-table>
      <div class="camera_outer">
        <video id="videoCamera" :width="videoWidth" :height="videoHeight" autoplay></video>
        <canvas style="display:none;" id="canvasCamera" :width="videoWidth" :height="videoHeight"></canvas>
        <div v-if="imgSrc" class="img_bg_camera">
          <p></p>
          <img :src="imgSrc" alt class="tx_img" />
        </div>
        <div class="button">
          <el-button size="medium" type="primary" @click="getCompetence()"></el-button>
          <el-button size="medium" type="danger" @click="stopNavigator()"></el-button>
          <el-button size="medium" type="primary" @click="setImage()"></el-button>
          <el-button size="medium" type="primary" @click="realnameverification()"></el-button>
        </div>
      </div>
      <!--  -->
      
    </el-card>
  </div>
</template>   



<script>
export default {
  data() {
    return {
      videoWidth: 200,
      videoHeight: 300,
      imgSrc: "",
      thisCancas: null,
      thisContext: null,
      thisVideo: null,
      openVideo: false,
      form: {},
      userlist:[]
    };
  },
  mounted() {
    //this.getCompetence()//
  },
  methods: {
    // 
    getCompetence() {
      var _this = this;
      _this.thisCancas = document.getElementById("canvasCamera");
      _this.thisContext = this.thisCancas.getContext("2d");
      _this.thisVideo = document.getElementById("videoCamera");
      _this.thisVideo.style.display = "block";
      // mediaDevices
      if (navigator.mediaDevices === undefined) {
        navigator.mediaDevices = {};
      }
      // mediaDevices
      // getUserMedia
      // getUserMedia
      if (navigator.mediaDevices.getUserMedia === undefined) {
        navigator.mediaDevices.getUserMedia = function(constraints) {
          // getUserMedia()
          var getUserMedia =
            navigator.webkitGetUserMedia ||
            navigator.mozGetUserMedia ||
            navigator.getUserMedia;
          // 
          // 
          if (!getUserMedia) {
            //
            return Promise.reject(
              new Error("getUserMedia is not implemented in this browser")
            );
          }
          // Promisenavigator.getUserMedia
          return new Promise(function(resolve, reject) {
            getUserMedia.call(navigator, constraints, resolve, reject);
          });
        };
      }
      var constraints = {
        audio: false,
        video: {
          width: this.videoWidth,
          height: this.videoHeight,
          transform: "scaleX(-1)"
        }
      };
      navigator.mediaDevices
        .getUserMedia(constraints)
        .then(function(stream) {
          // srcObject
          if ("srcObject" in _this.thisVideo) {
            _this.thisVideo.srcObject = stream;
          } else {
            // 
            _this.thisVideo.src = window.URL.createObjectURL(stream);
          }
          _this.thisVideo.onloadedmetadata = function(e) {
            _this.thisVideo.play();
          };
        })
        .catch(err => {
          console.log(err);
        });
    },
    //  
    setImage() {
      var _this = this;
      // canvas
      _this.thisContext.drawImage(
        _this.thisVideo,
        0,
        0,
        _this.videoWidth,
        _this.videoHeight
      );
      // base64
      var image = this.thisCancas.toDataURL("image/jpeg");
      _this.imgSrc = image; //
      //   console.log(image);
    },
    // 
    stopNavigator() {
      this.thisVideo.srcObject.getTracks()[0].stop();
    },
    // base64
    dataURLtoFile(dataurl, filename) {
      var arr = dataurl.split(",");
      var mime = arr[0].match(/:(.*?);/)[1];
      var bstr = atob(arr[1]);
      var n = bstr.length;
      var u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr], filename, { type: mime });
    },
    // 
    async realnameverification() {
      console.log("base64", this.imgSrc);
      let formData = new FormData();
      formData.append("file", this.imgSrc);
      const {
        data: res
      } = await this.$http.post(
        "http://101.132.135.179:8088/hotel/face/faceSearch",
        formData,
        { "Content-Type": "multipart/form-data" }
      );
      console.log(":", res);
      this.form = res.data;
      if (res.code == 0) {
        this.$message.success("");
      } else {
        this.$message.error("");
      }
      const { data: demo } = await this.$http.post(
        "http://101.132.135.179:8088/hotel/select/getUserRoomListByUserId?userId="+this.form.userId
      );
      if (demo.code == 0) {
          this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
      } else {
        this.$message.error("");
      }
      this.userlist=demo.data;
      console.log('',this.userlist)
    }
  }
};
</script>

<style>
</style>

