<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getGoodList">
            <el-button slot="append" icon="el-icon-search" @click="getGoodList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="goodlist" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="foodName" label=""></el-table-column>
        <el-table-column prop="restaurantPicture" label="">
          <template slot-scope="scope">
            <img :src="'http://101.132.135.179:8080'+scope.row.restaurantPicture" width="150px" height="100px">
          </template>
        </el-table-column>
        <el-table-column prop="restaurantIntro" label="" width="150px"></el-table-column>
        <el-table-column
          prop="restaurantType"
          label=""
          width="150"
          column-key="restaurantType"
          :filters="[{text: '', value: ''}, {text: '', value: ''}, {text: '', value: ''}, {text: '', value: ''}]"
        ></el-table-column>
        <el-table-column prop="foodUnitPrice" label="" sortable></el-table-column>
        <el-table-column label="" width="180px">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="showGoodDialog(scope.row.id)"
            ></el-button>
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="removeGoodById(scope.row.id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.page"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.limit"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>

      <el-row :gutter="20">
        <el-col :span="4">
          <el-button type="danger" style="margin:10px 0 0 0"></el-button>
        </el-col>
      </el-row>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
      <!--  -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="80px">
        <el-form-item label="" prop="foodName">
          <el-input v-model="addForm.foodName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="restaurantIntro">
          <el-input v-model="addForm.restaurantIntro"></el-input>
        </el-form-item>
        <el-form-item label="" prop="restaurantType">
          <el-input v-model="addForm.restaurantType"></el-input>
        </el-form-item>
        <el-form-item label="" prop="foodUnitPrice">
          <el-input v-model="addForm.foodUnitPrice"></el-input>
        </el-form-item>
        <el-form-item label="" prop="foodNum">
          <el-input v-model="addForm.foodNum"></el-input>
        </el-form-item>
        <el-form-item label="" prop="foodInstock">
          <el-input v-model="addForm.foodInstock"></el-input>
        </el-form-item>
        <el-form-item label="" prop="file">
          <el-upload
            ref="upload"
            action="http://101.132.135.179:8088/hotel/restaurant/insertRestaurant"
            :limit="limitNum"
            :auto-upload="false"
            accept=".jpg"
            :before-upload="beforeUploadFile"
            :on-change="fileChange"
            :on-exceed="exceedFile"
            :file-list="fileList"
          >
            <el-button size="small" plain></el-button>
            <div slot="tip" class="el-upload__tip" style="color:grey;">jpg10M</div>
          </el-upload>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addGood"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
        <el-form-item label="" prop="foodName">
          <el-input v-model="editForm.foodName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="restaurantIntro">
          <el-input v-model="editForm.restaurantIntro"></el-input>
        </el-form-item>
        <el-form-item label="" prop="restaurantType">
          <el-input v-model="editForm.restaurantType"></el-input>
        </el-form-item>
        <el-form-item label="" prop="foodUnitPrice">
          <el-input v-model="editForm.foodUnitPrice"></el-input>
        </el-form-item>
        <el-form-item label="" prop="foodInstock">
          <el-input v-model="editForm.foodInstock"></el-input>
        </el-form-item>
        <el-form-item label="" prop="file">
          <el-upload
            ref="upload"
            action="http://101.132.135.179:8088/hotel/restaurant/updateRestaurant"
            :limit="limitNum"
            :auto-upload="false"
            accept=".jpg"
            :before-upload="beforeUploadFile"
            :on-change="fileChange"
            :on-exceed="exceedFile"
            :file-list="fileList"
          >
            <el-button size="small" plain></el-button>
            <div slot="tip" class="el-upload__tip" style="color:grey;">jpg10M</div>
          </el-upload>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editGoodInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryInfo: {
        // query: "",
        // 
        page: 1,
        // 
        limit: 5
      },
      total: 0,
      goodlist: [],
      addDialogVisible: false,
      // 
      limitNum: 1,
      // 
      addForm: {
        file: "",
        foodName:'',
        restaurantType: "",
        restaurantIntro: "",
        foodUnitPrice: "",
        foodInstock: "",
        foodNum:''
      },
      fileList: [],
      // 
      addFormRules: {
        restaurantIntro: [
          { required: true, message: "", trigger: "blur" }
        ],
        restaurantType: [
          { required: true, message: "", trigger: "blur" }
        ],
        foodUnitPrice: [
          { required: true, message: "", trigger: "blur" }
        ],
        foodInstock: [
          { required: true, message: "", trigger: "blur" }
        ],
        foodName: [
          { required: true, message: "", trigger: "blur" }
        ],
        foodNum: [
          { required: true, message: "", trigger: "blur" }
        ],
      },
      // 
      editDialogVisible: false,
      // 
      editForm: {},
      // 
      editFormRules: {
         restaurantIntro: [
          { required: true, message: "", trigger: "blur" }
        ],
        restaurantType: [
          { required: true, message: "", trigger: "blur" }
        ],
        foodUnitPrice: [
          { required: true, message: "", trigger: "blur" }
        ],
        foodInstock: [
          { required: true, message: "", trigger: "blur" }
        ],
        foodName: [
          { required: true, message: "", trigger: "blur" }
        ],
        foodNum: [
          { required: true, message: "", trigger: "blur" }
        ],
      }
    };
  },
  created() {
    this.getGoodList();
  },
  methods: {
    async getGoodList() {
      const { data: res } = await this.$http.get(
        "http://101.132.135.179:8088/hotel/select/getRestaurantList",
        {
          params: this.queryInfo
        }
      );

      if (res.code !== 0) {
        return this.$message.error("");
      }
      this.$message.success("");

      console.log(res);
      this.total = res.count;
      this.goodlist = res.data;
    },
    handleSizeChange(newSize) {
      this.queryInfo.limit = newSize;
      this.getGoodList();
    },
    handleCurrentChange(newPage) {
      this.queryInfo.page = newPage;
      this.getGoodList();
    },
    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields();
      this.$refs.upload.clearFiles();
    },
    // 
    addGood() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return;
        let formData = new FormData();
        formData.append("file", this.addForm.file);
        // 
        const {
          data: res
        } = await this.$http.post(
          "http://101.132.135.179:8088/hotel/restaurant/insertRestaurant?" +
            "restaurantType=" +
            this.addForm.restaurantType +
            "&restaurantIntro=" +
            this.addForm.restaurantIntro +
            "&foodUnitPrice=" +
            this.addForm.foodUnitPrice +
            "&foodInstock=" +
            this.addForm.foodInstock +
            "&foodName=" +
            this.addForm.foodName+
            "&foodNum=" +
            this.addForm.foodNum,
          formData,
          { "Content-Type": "multipart/form-data" }
        );

        if (res.code == 0) {
          this.$message.success("");
        } else if (res.code == 1000) {
          this.$message.warning("");
        } else {
          this.$message.error("");
        }

        // 
        this.addDialogVisible = false;
        // 
        this.getGoodList();
      });
    },
    // 
    async showGoodDialog(id) {
      console.log(id)
     const { data: res } = await this.axios.post(
        "http://101.132.135.179:8088/hotel/select/getRestaurantById?id=" + id
      );

      console.log("id", res);
      if (res.code != 0) {
        return this.$message.success("");
      }
      this.$message.success("");
      this.editForm = res.data;
      console.log("", res.data);
      this.editDialogVisible = true;
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields();
      this.$refs.upload.clearFiles();
    },
    // 
    editGoodInfo() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return;
        // 
        let formData1 = new FormData();
        formData1.append("file", this.editForm.file);
        const { data: res } = this.$http
          .post(
            "http://101.132.135.179:8088/hotel/restaurant/updateRestaurant?id=" +
              this.editForm.id +
              "&restaurantType=" +
              this.editForm.restaurantType +
              "&restaurantIntro=" +
              this.editForm.restaurantIntro +
              "&foodUnitPrice=" +
              this.editForm.foodUnitPrice +
              "&foodInstock=" +
              this.editForm.foodInstock+
               "&foodName=" +
              this.editForm.foodName,
            formData1,
            { "Content-Type": "multipart/form-data" }
          )
          .then(res => {
            if (res.data.code !== 0) {
              return this.$message.error("");
            }
            // 
            this.$message.success("");
            // 
            this.editDialogVisible = false;
            // 
            this.getGoodList();
          })
          .catch(err => {});
      });
    },
    // Id
    async removeGoodById(id) {
      console.log(id)
      // 
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => err);

      //  confirm
      //  cancel
      // console.log(confirmResult)
      if (confirmResult !== "confirm") {
        return this.$message.info("");
      }

       const { data: res } = await this.axios
        .post("http://101.132.135.179:8088/hotel/restaurant/recycleRestaurant?id=" + id)
        .then(res => {
          console.log("res", res);
          if (res.code != 0) {
            return this.$message.success("");
          }
          this.$message.error("");
        });
      this.getGoodList();
    },
    // 
    exceedFile(files, fileList) {
      this.$notify.warning({
        title: "",
        message: ` ${
          this.limitNum
        }  ${files.length + fileList.length} `
      });
    },
    // 
    fileChange(file, fileList) {
      console.log("change");
      console.log(file);
      this.addForm.file = file.raw;
      console.log(this.addForm.file);
      console.log(fileList);
    },
    // , , false  Promise  reject
    beforeUploadFile(file) {
      console.log("before upload");
      console.log(file);
      let extension = file.name.substring(file.name.lastIndexOf(".") + 1);
      let size = file.size / 1024 / 1024;
      if (extension !== "jpg") {
        this.$notify.warning({
          title: "",
          message: `jpg.jpg`
        });
      }
      if (size > 10) {
        this.$notify.warning({
          title: "",
          message: `10M`
        });
      }
    }
    // // 
    // handleSuccess(res, file, fileList) {
    //   this.$notify.success({
    //     title: "",
    //     message: ``
    //   });
    // },
    // // 
    // handleError(err, file, fileList) {
    //   this.$notify.error({
    //     title: "",
    //     message: ``
    //   });
    // }
  }
};
</script>

<style lang="stylus">
.el-cascader {
  width: 100%;
}
</style>
