<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getRoomList">
            <el-button slot="append" icon="el-icon-search" @click="getRoomList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="roomlist" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="roomFloor" label="" width="80px"></el-table-column>
        <el-table-column prop="roomNumber" label="" width="75px"></el-table-column>
        <el-table-column prop="roomPicture" label="">
          <template slot-scope="scope">
            <img :src="'http://101.132.135.179:8080'+scope.row.roomPicture" width="90px" height="100px"/>
          </template>
        </el-table-column>
        <el-table-column prop="roomIntro" label="" width="150px"></el-table-column>
        <el-table-column
          prop="roomType"
          label=""
          width="90px"
          column-key="roomType"
          :filters="[{text: '', value: ''}, {text: '', value: ''}, {text: '', value: ''}]"
        ></el-table-column>
        <el-table-column
          prop="roomBedType"
          label=""
          width="80px"
          column-key="roomBedType"
          :filters="[{text: '', value: ''}, {text: '', value: ''}]"
        ></el-table-column>
        <el-table-column prop="roomPrice" label="" sortable></el-table-column>
        <el-table-column  label="" prop="roomStates">
           <template slot-scope="scope">
            <el-tag v-if="scope.row.roomStates === 3"></el-tag>
            <el-tag type="success" v-else-if="scope.row.roomStates === 1"></el-tag>
            <el-tag type="warning" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" width="180px">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="showRoomDialog(scope.row.id)"
            ></el-button>
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="removeRoomById(scope.row.id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.page"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.limit"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>

      <el-row :gutter="20">
        <el-col :span="4">
          <el-button type="danger" style="margin:10px 0 0 0"></el-button>
        </el-col>
      </el-row>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
      <!--  -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="80px">
        <el-form-item label="" prop="roomFloor">
          <el-input v-model="addForm.roomFloor"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roomNumber">
          <el-input v-model="addForm.roomNumber"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roomIntro">
          <el-input v-model="addForm.roomIntro"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roomType">
          <el-input v-model="addForm.roomType"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roomBedType">
          <el-input v-model="addForm.roomBedType"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roomPrice">
          <el-input v-model="addForm.roomPrice"></el-input>
        </el-form-item>
        <el-form-item label="" prop="file">
          <el-upload
            ref="upload"
            action="http://101.132.135.179:8088/hotel/room/insertRoom"
            :limit="limitNum"
            :auto-upload="false"
            accept=".jpg"
            :before-upload="beforeUploadFile"
            :on-change="fileChange"
            :on-exceed="exceedFile"
            :file-list="fileList"
          >
            <el-button size="small" plain></el-button>
            <div slot="tip" class="el-upload__tip" style="color:grey;">jpg10M</div>
          </el-upload>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addRoom"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
        <el-form-item label="" prop="roomFloor">
          <el-input v-model="editForm.roomFloor"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roomNumber">
          <el-input v-model="editForm.roomNumber"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roomIntro">
          <el-input v-model="editForm.roomIntro"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roomType">
          <el-input v-model="editForm.roomType"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roomBedType">
          <el-input v-model="editForm.roomBedType"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roomPrice">
          <el-input v-model="editForm.roomPrice"></el-input>
        </el-form-item>
        <el-form-item label="" prop="file">
          <el-upload
            ref="upload"
            action="http://101.132.135.179:8088/hotel/room/updateRoom"
            :limit="limitNum"
            :auto-upload="false"
            accept=".jpg"
            :before-upload="beforeUploadFile"
            :on-change="fileChange"
            :on-exceed="exceedFile"
            :file-list="fileList"
          >
            <el-button size="small" plain></el-button>
            <div slot="tip" class="el-upload__tip" style="color:grey;">jpg10M</div>
          </el-upload>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editGoodrInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryInfo: {
        // query: "",
        // 
        page: 1,
        // 
        limit: 5
      },
      total: 0,
      roomlist: [],
      addDialogVisible: false,
      // 
      limitNum: 1,
      // 
      addForm: {
        file: "",
        roomFloor: "",
        roomNumber: "",
        roomIntro: "",
        roomType: "",
        roomPrice: "",
        roomBedType:""
      },
      fileList: [],
      // 
      addFormRules: {
        roomFloor: [
          { required: true, message: "", trigger: "blur" }
        ],
        roomNumber: [
          { required: true, message: "", trigger: "blur" }
        ],
        roomIntro: [
          { required: true, message: "", trigger: "blur" }
        ],
        roomType: [
          { required: true, message: "", trigger: "blur" }
        ],
        roomPrice: [
          { required: true, message: "", trigger: "blur" }
        ],
        roomBedType: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      // 
      editDialogVisible: false,
      // 
      editForm: {},
      // 
      editFormRules: {}
    };
  },
  created() {
    this.getRoomList();
  },
  methods: {
    async getRoomList() {
      const { data: res } = await this.$http.get(
        "http://101.132.135.179:8088/hotel/select/getRoomList",
        {
          params: this.queryInfo
        }
      );

      if (res.code !== 0) {
        return this.$message.error("");
      }
      this.$message.success("");

      console.log(res);
      this.total = res.data.length;
      this.roomlist = res.data;
    },
    handleSizeChange(newSize) {
      this.queryInfo.limit = newSize;
      this.getRoomList();
    },
    handleCurrentChange(newPage) {
      this.queryInfo.page = newPage;
      this.getRoomList();
    },
    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields();
      this.$refs.upload.clearFiles();
    },
    // 
    addRoom() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return;
        let formData = new FormData();
        formData.append("file", this.addForm.file);
        // 
        const {
          data: res
        } = await this.$http.post(
          "http://101.132.135.179:8088/hotel/room/insertRoom?" +
            "roomFloor=" +
            this.addForm.roomFloor +
            "&roomNumber=" +
            this.addForm.roomNumber +
            "&roomIntro=" +
            this.addForm.roomIntro +
            "&roomType=" +
            this.addForm.roomType +
            "&roomPrice=" +
            this.addForm.roomPrice+
            "&roomBedType=" +
            this.addForm.roomBedType,
          formData,
          { "Content-Type": "multipart/form-data" }
        );

        if (res.code == 0) {
          this.$message.success("");
        } else if (res.code == 1000) {
          this.$message.warning("");
        } else {
          this.$message.error("");
        }

        // 
        this.addDialogVisible = false;
        // 
        this.getRoomList();
      });
    },
    // 
    async showRoomDialog(id) {
      console.log(id);
      const { data: res } = await this.axios.post(
        "http://101.132.135.179:8088/hotel/select/getRoomById?id=" + id
      );

      console.log("id", res);
      if (res.code != 0) {
        return this.$message.success("");
      }
      this.$message.success("");
      this.editForm = res.data;
      console.log("", res.data);
      this.editDialogVisible = true;
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields();
      this.$refs.upload.clearFiles();
    },
    // 
    editGoodrInfo() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return;
        // 
        let formData1 = new FormData();
        formData1.append("file", this.editForm.file);
        const { data: res } = this.$http
          .post(
            "http://101.132.135.179:8088/hotel/room/updateRoom?id=" +
              this.editForm.id +
              "&roomFloor=" +
              this.editForm.roomFloor +
              "&roomNumber=" +
              this.editForm.roomNumber +
              "&roomIntro=" +
              this.editForm.roomIntro +
              "&roomType=" +
              this.editForm.roomType +
              "&roomBedType=" +
              this.editForm.roomBedType+
              "&roomPrice=" +
              this.editForm.roomPrice,
            formData1,
            { "Content-Type": "multipart/form-data" }
          )
          .then(res => {
            if (res.data.code !== 0) {
              return this.$message.error("");
            }
            // 
            this.$message.success("");
            // 
            this.editDialogVisible = false;
            // 
            this.getRoomList();
          })
          .catch(err => {});
      });
    },
    // Id
    async removeRoomById(id) {
      console.log(id);
      // 
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => err);

      //  confirm
      //  cancel
      // console.log(confirmResult)
      if (confirmResult !== "confirm") {
        return this.$message.info("");
      }

      const { data: res } = await this.axios
        .post(
          "http://101.132.135.179:8088/hotel/room/recycleRoom?id=" + id
        )
        .then(res => {
          console.log("res", res);
          if (res.code != 0) {
            return this.$message.success("");
          }
          this.$message.error("");
        });
      this.getRoomList();
    },
    // 
    exceedFile(files, fileList) {
      this.$notify.warning({
        title: "",
        message: ` ${
          this.limitNum
        }  ${files.length + fileList.length} `
      });
    },
    // 
    fileChange(file, fileList) {
      console.log("change");
      console.log(file);
      this.addForm.file = file.raw;
      console.log(this.addForm.file);
      console.log(fileList);
    },
    // , , false  Promise  reject
    beforeUploadFile(file) {
      console.log("before upload");
      console.log(file);
      let extension = file.name.substring(file.name.lastIndexOf(".") + 1);
      let size = file.size / 1024 / 1024;
      if (extension !== "jpg") {
        this.$notify.warning({
          title: "",
          message: `jpg.jpg`
        });
      }
      if (size > 10) {
        this.$notify.warning({
          title: "",
          message: `10M`
        });
      }
    }
    // // 
    // handleSuccess(res, file, fileList) {
    //   this.$notify.success({
    //     title: "",
    //     message: ``
    //   });
    // },
    // // 
    // handleError(err, file, fileList) {
    //   this.$notify.error({
    //     title: "",
    //     message: ``
    //   });
    // }
  }
};
</script>

<style lang="stylus">
.el-cascader {
  width: 100%;
}
</style>
